.navbar-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.navbar-list-item {
  -ms-flex-item-align: center;
      align-self: center;
  margin: 12px;
  list-style-type: none;
}

.city-name {
