.search-button {
  padding: 8px;
  background-color: #fe1743;
  color: white;
  cursor: pointer;
  border: none;
  border-bottom: 1px solid #fe1743;
}

.search-button:focus {
  outline: none;
}


form {
  border-bottom: 1px solid #fe1743;
}

input[type='text'] {
  padding: 8px;
  border: none;
  outline: none;
  background: transparent;
  color: white;
  caret-color: white;
}



::-webkit-input-placeholder {
  color: white;
}
:-ms-input-placeholder {
  color: white;
}
::-ms-input-placeholder {
  color: white;
}
::placeholder {
  color: white;
}




